<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3 heading">Car Wash Dispatch</h1>
      </div>
      <div class="card col-md-3" v-for="(req, index) in reqs" :key="index">
        <div class="card-body">
          <h5 class="card-title">Stock #: {{req.stock_number}}</h5>
          <p class="card-text">Sales Associate: {{req.sales_associate}}</p>
          <p class="card-text">Customer Name: {{req.customer_name}}</p>
          <p class="card-text">Type of Vehicle: {{req.type_of_vehicle}}</p>
          <p class="card-text">Year: {{req.year}}</p>
          <p class="card-text">Model: {{req.model}}</p>
          <p class="card-text">License Plate Bracket: {{req.license_plate}}</p>
          <p class="card-text">Car Wash Associate: {{req.carwash_associate}}</p>
          <hr />
          <!-- input fields -->
          <label v-if="req.carwash_state" for="status_carwash">Status</label>
          <select
            id="status_carwash"
            v-if="req.carwash_state"
            class="form-control form-control-lg"
            v-model="req.status_carwash"
            required
          >
            <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
          </select>
          <hr v-if="req.carwash_state" />
          <label v-if="req.carwash_state" for="carwash_associate">Car Wash Associate</label>
          <select
            id="carwash_associate"
            v-if="req.carwash_state"
            class="form-control form-control-lg"
            v-model="req.carwash_associate"
            required
          >
            <option v-for="(man, index) in managers" :key="index">{{ man }}</option>
          </select>
          <div v-if="!req.carwash_state">
            <label v-if="!req.carwash_state" for="mats">Are the mats in the car?</label>
            <div class="custom-control custom-checkbox custom-control-inline">
              <input
                type="checkbox"
                class="custom-control-input"
                id="mats"
                v-model="req.checked_mats"
              />
              <label class="custom-control-label" for="mats">Yes</label>
            </div>
          </div>
          <div v-if="!req.carwash_state">
            <label v-if="!req.carwash_state" for="license_plate">License Plate Bracket Installed?</label>
            <div class="custom-control custom-checkbox custom-control-inline">
              <input
                type="checkbox"
                class="custom-control-input"
                id="license_plate"
                v-model="req.license_plate_installed"
              />
              <label class="custom-control-label" for="license_plate">Yes</label>
            </div>
          </div>
          <hr v-if="!req.carwash_state" />
          <label v-if="!req.carwash_state" for="delivery_bay">Delivery Bay</label>
          <select
            v-if="!req.carwash_state"
            id="delivery_bay"
            class="form-control form-control-lg"
            v-model="req.delivery_bay"
          >
            <option v-for="(del, index) in delivery" :key="index">{{ del }}</option>
          </select>
          <v-btn
            v-if="req.carwash_state"
            @click="claimed(req.id, req.carwash_associate, req.status_carwash)"
            color="success"
          >Claim</v-btn>
          <v-btn outline color="error" v-if="!req.carwash_state" @click="unclaimed(req.id)">Unclaim</v-btn>
          <v-btn
            v-if="!req.carwash_state"
            @click="complete(req.id, req.checked_mats, req.license_plate_installed, req.delivery_bay)"
            color="success"
          >Complete</v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Finance",
  data() {
    return {
      reqs: [],
      approvals: ["Claimed"],
      managers: [
        "Car Wash Associate 1",
        "Car Wash Associate 2",
        "Car Wash Associate 3",
        "Car Wash Associate 4"
      ],
      delivery: ["Bay 1", "Bay 2", "Bay 3", "Bay 4", "Delivery Bay Overflow"],
      mats: ["Yes", "No"],
      completionSales: "",
      salesManager: "",
      claimedStatus: "",
      carwashassociate: "",
      delivery_bay: ""
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("status_sales", "==", "Approved")
        .where("send_to_carwash", "==", true)
    };
  },
  methods: {
    claimed(id, associate, status) {
      db.collection("makeReady")
        .doc(id)
        .update({
          carwash_associate: associate,
          carwash_claimed_timestamp: new Date(),
          status_carwash: status,
          carwash_state: false
        });
    },
    unclaimed(id) {
      db.collection("makeReady")
        .doc(id)
        .update({
          carwash_associate: "",
          carwash_claimed_timestamp: "",
          status_carwash: "",
          carwash_state: true
        });
    },
    complete(id, checked_mats, license_plate_installed, delivery_bay) {
      db.collection("makeReady")
        .doc(id)
        .update({
          status_carwash: "Complete",
          carwash_complete_timestamp: new Date(),
          delivery_bay: delivery_bay,
          checked_mats: checked_mats,
          license_plate_installed: license_plate_installed,
          send_to_carwash: false
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 15px;
}

.container-fluid {
  width: 90%;
}

.heading {
  margin-bottom: 5rem;
  margin-top: 5rem;
}
</style>



