<template>
  <div>
    <v-toolbar dark color="blue-grey darken-2">
      <v-toolbar-side-icon class="hidden-md-and-up" @click="toggleDrawer"></v-toolbar-side-icon>
      <v-toolbar-title>BMW of San Antonio Make Ready</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn v-for="(link, index) in links" :key="index" :to="link.path" flat>{{link.title}}</v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-navigation-drawer
      :clipped="drawer.clipped"
      :permanent="drawer.permanent"
      v-model="drawer.open"
      app
    >
      <v-toolbar flat>
        <v-list>
          <v-list-tile>
            <v-list-tile-title class="title">Menu</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-toolbar>

      <v-divider></v-divider>

      <v-list dense class="pt-0">
        <v-list-tile v-for="(link, index) in links" :key="index" :to="link.path">
          <v-list-tile-content>
            <v-list-tile-title>{{ link.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      drawer: {
        // sets the open status of the drawer
        open: false,
        // sets if the drawer is shown above (false) or below (true) the toolbar
        clipped: false,
        // sets if the drawer remains visible all the time (true) or not (false)
        permanent: false
      },
      links: [
        {
          title: "Request",
          path: "/"
        },
        {
          title: "New Car",
          path: "/newcar"
        },
        {
          title: "TPO",
          path: "/preowned"
        },
        {
          title: "Loan Car",
          path: "/loancar"
        },
        {
          title: "Parts",
          path: "/parts"
        },
        {
          title: "Finance",
          path: "/finance"
        },
        {
          title: "Car Wash",
          path: "/carwash"
        },
        {
          title: "Metrics",
          path: "/metrics"
        }
      ]
    };
  },
  methods: {
    toggleDrawer() {
      if (this.drawer.permanent) {
        this.drawer.permanent = !this.drawer.permanent;
        // set the clipped state of the drawer and toolbar
        this.drawer.clipped = true;
        this.toolbar.clippedLeft = true;
      } else {
        // normal drawer
        this.drawer.open = !this.drawer.open;
      }
    }
  }
};
</script>

<style scoped>
.v-navigation-drawer > .v-list .v-list__tile__title {
  height: 60px;
}
.v-list__tile__title {
  font-size: 30px;
}
</style>





