import Vue from "vue";
import Router from "vue-router";
import Request from "@/components/Request";
import Newcar from "@/components/Newcar";
import Loaner from "@/components/Loaner";
import Preowned from "@/components/Preowned";
import Finance from "@/components/Finance";
import Carwash from "@/components/Carwash";
import Parts from "@/components/Parts";
import Metrics from "@/components/Metrics";
import SuccessfulSubmission from "@/components/SuccessfulSubmission";
import SignUp from "@/components/SignUp";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Request",
      component: Request
    },
    {
      path: "/newcar",
      name: "Newcar",
      component: Newcar
    },
    {
      path: "/loancar",
      name: "Loaner",
      component: Loaner
    },
    {
      path: "/preowned",
      name: "Preowned",
      component: Preowned
    },
    {
      path: "/finance",
      name: "Finance",
      component: Finance
    },
    {
      path: "/carwash",
      name: "Carwash",
      component: Carwash
    },
    {
      path: "/parts",
      name: "Parts",
      component: Parts
    },
    {
      path: "/metrics",
      name: "Metrics",
      component: Metrics
    },
    {
      path: "/submission",
      name: "SuccessfulSubmission",
      component: SuccessfulSubmission
    },
    {
      path: "/signup",
      name: "SignUp",
      component: SignUp
    }
  ]
});



