<template>
  <div class="container-fluid">
    <div class="row">
      <input type="email" v-model="email" />
      <input type="password" v-model="pass" />
      <button @click.prevent="signup">Sign Up</button>
      <h3>{{test}}</h3>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import firebase from "firebase";
import { EventBus } from "../event-bus";

export default {
  name: "SignUp",
  data() {
    return {
      reqs: [],
      email: "",
      pass: "",
      user: "",
      test: ""
    };
  },
  methods: {
    // this is google sign in. I can get a log in, need to figure out how to know people are logged in and how to keep them logged in. Also how to send the information to other components.
    signup() {
      const provider = new firebase.auth.GoogleAuthProvider();

      firebase
        .auth()
        .signInWithPopup(provider)
        .then(result => {
          console.log(result);
          this.user = result.additionalUserInfo.profile.family_name;
          EventBus.$emit("clicked-event", this.user);
          this.$router.replace({ path: "/" });
        })
        .catch(err => {
          alert("Oops. " + err.message);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



