<template>
  <table class="table">
    <thead class="thead-dark">
      <tr>
        <th scope="col">#</th>
        <th scope="col">Sales Associate</th>
        <th scope="col">Customer Name</th>
        <th scope="col">Sales Status</th>
        <th scope="col">Parts Status</th>
        <th scope="col">Finance Status</th>
        <th scope="col">Car Wash Status</th>
        <th scope="col">Delivery Bay</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(req, index) in reqs" :key="index">
        <th>{{index + 1}}</th>
        <td>{{req.sales_associate}}</td>
        <td>{{req.customer_name}}</td>

        <td class="table-warning" v-if="req.status_sales == 'In progress...'">{{req.status_sales}}</td>
        <td class="table-success" v-if="req.status_sales == 'Approved'">{{req.status_sales}}</td>
        <td class="table-danger" v-if="req.status_sales == 'Denied'">{{req.status_sales}}</td>

        <td class="table-warning" v-if="req.status_parts == 'In progress...'">{{req.status_parts}}</td>
        <td class="table-success" v-if="req.status_parts == 'Complete'">{{req.status_parts}}</td>
        <td class="table-primary" v-if="req.status_parts == 'Claimed'">{{req.status_parts}}</td>

        <td
          class="table-warning"
          v-if="req.status_finance == 'In progress...'"
        >{{req.status_finance}}</td>
        <td class="table-success" v-if="req.status_finance == 'Complete'">{{req.status_finance}}</td>
        <td
          class="table-primary"
          v-if="req.status_finance == 'Claimed'"
        >{{req.status_finance}} by {{ req.finance_associate }}</td>

        <td
          class="table-warning"
          v-if="req.status_carwash == 'In progress...'"
        >{{req.status_carwash}}</td>
        <td class="table-success" v-if="req.status_carwash == 'Complete'">{{req.status_carwash}}</td>
        <td class="table-primary" v-if="req.status_carwash == 'Claimed'">{{req.status_carwash}}</td>
        <td>{{req.delivery_bay}}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Finance",
  data() {
    return {
      reqs: []
    };
  },
  firestore() {
    return {
      reqs: db.collection("makeReady").orderBy("request_timestamp", "desc")
    };
  },
  methods: {
    dateFormat(date) {
      return new Date(date.seconds * 1000).toLocaleString();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.approved {
  background-color: green;
}

.inprogress {
  background-color: yellow;
}

.denied {
  background-color: red;
}
</style>



