<template>
  <div class="container-fluid">
    <div class="row">
      <h1 class='display-3'>Request Submitted Successfully!</h1>
      <p>Your submission was successful! Please speak with a sales manager in order to get your request approved.</p>
      <p>You can track your request by clicking on the track request button below.</p>
      <router-link tag="span" to="/metrics">
        <v-btn>Track Your Request</v-btn>
      </router-link>
      <router-link tag="span" to="/">
        <v-btn>Submit a New Request</v-btn>
      </router-link>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "SuccessfulSubmission",
  data() {
    return {
      reqs: [],
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("type_of_vehicle", "==", "New")
        .where("completionSales", "==", "Incomplete")
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

h1 {
    margin-bottom: 2rem;
}

a {
    margin-top: 2rem;
}

.container-fluid {
    width: 90%;
}

.row {
    text-align: center;
    display: block;
}

.btn-primary {
  background-color: #0096a5;
  color: white!important;
}

</style>



