<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12">
        <h1 class="display-3 heading">Make Ready Request Form</h1>
      </div>
      <form
        class="col-sm-12"
        @submit.prevent="newReq(sales_associate, type_of_vehicle, deal_number, customer_name, stock_number, mileage, model, customer_care_package, accessories, list_accessories, year, license_plate)"
      >
        <div class="form-row">
          <div class="form-group col-md-12">
            <label for="sales_associate">Sales Associate</label>
            <select
              id="sales_associate"
              class="form-control form-control-lg"
              v-model="sales_associate"
              required
            >
              <option v-for="(associate, index) in salesAssociates" :key="index">{{ associate }}</option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-4">
            <label for="typeOfVehicle">Type Of Vehicle Sold</label>
            <select
              id="typeOfVehicle"
              class="form-control form-control-lg"
              v-model="type_of_vehicle"
              required
            >
              <option v-for="(type, index) in typeOfVehicle" :key="index">{{ type }}</option>
            </select>
          </div>
          <div class="form-group col-md-4">
            <label for="model">Vehicle Model</label>
            <select id="model" class="form-control form-control-lg" v-model="model" required>
              <option v-for="(vehicle, index) in vehicles" :key="index">{{ vehicle }}</option>
            </select>
          </div>
          <div class="form-group col-md-4">
            <label for="model">Vehicle Year</label>
            <select id="year" class="form-control form-control-lg" v-model="year" required>
              <option v-for="(year, index) in years" :key="index">{{ year }}</option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="stock_number">Stock Number</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="stock_number"
              placeholder="ex: 123456"
              v-model="stock_number"
              required
            />
          </div>
          <div class="form-group col-md-6">
            <label for="mileage">Mileage</label>
            <input
              class="form-control form-control-lg"
              id="mileage"
              placeholder="ex: 123,000"
              v-model="mileage"
              required
            />
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="deal_number">Deal Number</label>
            <input
              class="form-control form-control-lg"
              id="deal_number"
              placeholder="ex: 123456"
              v-model="deal_number"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="customer_name">Customer Name</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="customer_name"
              placeholder="ex: John Smith"
              v-model="customer_name"
              required
            />
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-4">
            <label for="customer_care_package">Customer Care Package</label>
            <select
              id="customer_care_package"
              class="form-control form-control-lg"
              v-model="customer_care_package"
              required
            >
              <option v-for="(boo, index) in boolean" :key="index">{{ boo }}</option>
            </select>
          </div>
          <div class="form-group col-md-4">
            <label for="license_plate">License Plate Bracket</label>
            <select
              id="license_plate"
              class="form-control form-control-lg"
              v-model="license_plate"
              required
            >
              <option v-for="(boo, index) in boolean" :key="index">{{ boo }}</option>
            </select>
          </div>
          <div class="form-group col-md-4">
            <label for="accessories">Accessories</label>
            <select
              id="accessories"
              class="form-control form-control-lg"
              v-model="accessories"
              required
            >
              <option v-for="(boo, index) in boolean" :key="index">{{ boo }}</option>
            </select>
          </div>
        </div>
        <div class="form-row" v-if="accessories == 'Yes'">
          <div class="form-group col-md-12">
            <label for="list_accessories">List Accessories</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="list_accessories"
              placeholder="ex: Black kidney grilles..."
              v-model="list_accessories"
              required
            />
          </div>
        </div>
        <v-btn outline v-if="sales_associate !== ''" @click.prevent="clear" color="error">Clear</v-btn>
        <v-btn type="submit" color="success">Submit</v-btn>
      </form>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";
import { EventBus } from "../event-bus.js";

export default {
  name: "Request",
  data() {
    return {
      reqs: [],
      salesAssociates: [
        "Pedro Davila",
        "Michael Polak",
        "Jerry Agulair",
        "Oscar Garcia",
        "Chris Quincena"
      ],
      typeOfVehicle: ["New", "Pre-owned", "Loaner"],
      vehicles: [
        "Non BMW",
        "1 series",
        "2 series",
        "3 series",
        "4 series",
        "5 series",
        "6 series",
        "7 series",
        "8 series",
        "X1",
        "X2",
        "X3",
        "X4",
        "X5",
        "X6",
        "X7",
        "Z4",
        "i8",
        "i3",
        "M"
      ],
      years: [
        "2019",
        "2018",
        "2017",
        "2016",
        "2015",
        "2014",
        "2013",
        "2012",
        "2011",
        "2010",
        "2009",
        "2008",
        "2007",
        "2006",
        "2005",
        "2004",
        "2003",
        "2002",
        "2001",
        "2000"
      ],
      boolean: ["Yes", "No"],
      // Form Values
      sales_associate: "",
      type_of_vehicle: "",
      deal_number: "",
      customer_name: "",
      stock_number: "",
      mileage: "",
      model: "",
      customer_care_package: "",
      accessories: "",
      list_accessories: "",
      license_plate: "",
      year: "",
      // sendToParts() gets run and determines if this will go to parts or not. This is due to the limitation of the query system in Google's Firebase.
      parts: ""
    };
  },
  firestore() {
    return {
      reqs: db.collection("makeReady")
    };
  },
  // Passing user infromation from the sign in page. Will figure this out later.
  // created() {
  //   EventBus.$on("clicked-event", user => {
  //     this.profile = user;
  //   });
  // },
  methods: {
    // Determine if the request should be sent to parts or not. Will store true or false in a variable. This variable is what parts is querying off of. This is due to the limitations of Google's Firebase.
    sendToParts() {
      if (
        this.type_of_vehicle == "New" ||
        this.type_of_vehicle == "Loaner" ||
        this.accessories == "Yes" ||
        this.customer_care_package == "Yes"
      ) {
        this.parts = true;
      } else {
        this.parts = false;
      }
    },
    // Sending form information and all fields needed in the make ready process.
    newReq(
      sales_associate,
      type_of_vehicle,
      deal_number,
      customer_name,
      stock_number,
      mileage,
      model,
      customer_care_package,
      accessories,
      list_accessories,
      year,
      license_plate
    ) {
      this.sendToParts();
      db.collection("makeReady").add({
        // form values start
        sales_associate,
        type_of_vehicle,
        deal_number,
        customer_name,
        stock_number,
        year,
        mileage,
        model,
        customer_care_package,
        accessories,
        list_accessories,
        license_plate,
        // form values end
        // Initial Request Timestamp Variable
        request_timestamp: new Date(),
        // Parts variables start
        parts_associate: "",
        status_parts: "",
        parts_completion_timestamp: "",
        parts_claimed_timestamp: "",
        send_to_parts: this.parts,
        invoice_number: "",
        mats: "",
        mats_shelf: "",
        parts_state: true,
        // Parts variables end
        // Finance variables start
        finance_completion_timestamp: "",
        finance_claimed_timestamp: "",
        finance_associate: "",
        status_finance: "",
        send_to_finance: true,
        finance_state: true,
        // Finance variables end
        // Carwash variables start
        carwash_claimed_timestamp: "",
        carwash_complete_timestamp: "",
        status_carwash: "",
        delivery_bay: "",
        checked_mats: "",
        carwash_associate: "",
        send_to_carwash: true,
        carwash_state: true,
        license_plate_installed: "",
        // Carwash variables end
        // Sales variables start
        sales_completion_timestamp: "",
        status_sales: "In progress...",
        sales_manager: "",
        password: ""
        // Sales variables end
      });
      (this.sales_associate = ""),
        (this.type_of_vehicle = ""),
        (this.deal_number = ""),
        (this.customer_name = ""),
        (this.stock_number = ""),
        (this.mileage = ""),
        (this.model = ""),
        (this.customer_care_package = ""),
        (this.accessories = ""),
        (this.list_accessories = ""),
        (this.year = "");
      this.$router.push("/submission");
    },
    clear() {
      (this.sales_associate = ""),
        (this.type_of_vehicle = ""),
        (this.deal_number = ""),
        (this.customer_name = ""),
        (this.stock_number = ""),
        (this.mileage = ""),
        (this.model = ""),
        (this.customer_care_package = ""),
        (this.accessories = ""),
        (this.list_accessories = ""),
        (this.year = "");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

.form-group {
  margin-bottom: 20px;
}

button {
  margin-top: 3rem;
  margin-bottom: 50px;
  margin-left: 20px;
  height: 3.5rem;
  width: 20%;
}

label {
  font-weight: bold;
  font-size: 1.2em;
}

.form-row {
  margin-bottom: 2rem;
}

.heading {
  margin-bottom: 5rem;
  margin-top: 5rem;
}
</style>



